import React, { useState } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { Route, Link } from "react-router-dom";
// components
import AddIcon from "@material-ui/icons/Add";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import DeleteTwoToneIcon from "@material-ui/icons/DeleteTwoTone";
import SubjectIcon from "@material-ui/icons/Subject";
// papges
import CreateTask from "../CreatTask/CreateTask";
import TaskList from "../TaskList/TaskList";
import RecycleBin from "../RecycleBin/RecycleBin";
import Commission from "../Commission/Commission.jsx";
import Questionnaire from "../Questionnaire/Questionnaire.jsx";

const styles = {
  contentWrapper: {
    top: "1rem"
  },
  buttonWrapper: {
    maxWidth: 400,
    margin: "auto",
    overflow: "hidden"
  }
};

/*

{
  [
    taskName: ""
    taskID: "ID"
    taskState: " /  / "
    taskType: ""
    number: 100         //  
    finishedNumber: 30 // 
    publishTime: ""    // 
    endTime: ""       // 
  ]
}
*/
function TaskBoard(props) {

  if (!localStorage.getItem("user-token")) {
    props.history.push("/login");
  }
  // eslint-disable-next-line react/prop-types
  const { classes, match, history } = props;

  // eslint-disable-next-line no-console
  // = 1   = 2 
  const [ToTask, setToTask] = useState(0); // ;
  const [taskID, setTaskID] = useState(""); // taskID

  const createTask = "";
  const nullTaskID = "";
  const updateTask = "";

  // 
  function transferMsg(msg) {
    // eslint-disable-next-line no-console
    console.log("transfer the msg: " + msg);
    if (msg === "2Q") {
      // 
      setToTask(1);
    } else if (msg === "2C") {
      // 
      setToTask(2);
    } else if (msg === "Return") {
      // 
      setToTask(0);
    } else if (msg.length === 14) {
      // 
      // {`${match.path}/updatetask/Questionnaire`}
      let url = match.url + "/updatetask/Questionnaire";
      setTaskID(msg);
      history.push(url); // 
      setToTask(1);
    }
  }

  return (
    <div>
      {ToTask === 0 && (
        <div>
          <Grid container className={classes.buttonWrapper}>
            <Grid item xs={4}>
              <Link to={`${match.url}`}>
                <Button variant="contained" color="primary">
                  <SubjectIcon />
                  
                </Button>
              </Link>
            </Grid>
            <Grid item xs={4}>
              <Link to={`${match.url}/createtask`}>
                <Button variant="contained" color="primary">
                  <AddIcon /> 
                </Button>
              </Link>
            </Grid>
            <Grid item xs={4}>
              <Link to={`${match.url}/recyclebin`}>
                <Button variant="contained" color="primary">
                  <DeleteTwoToneIcon />
                  
                </Button>
              </Link>
            </Grid>
          </Grid>
        </div>
      )}
      <Route
        exact
        path={`${match.path}`}
        component={() => <TaskList transferMsg={transferMsg} history={history} />}
      />
      <Route
        path={`${match.path}/createtask`}
        component={() => (
          <CreateTask
            transferMsg={transferMsg}
            path={match.url}
            display={ToTask === 0}
          />
        )}
      />
      <Route path={`${match.path}/recyclebin`} component={RecycleBin} />
      <Route
        path={`${match.path}/createtask/Questionnaire`}
        component={() => (
          <Questionnaire
            transferMsg={transferMsg}
            path={match.url}
            taskID={nullTaskID}
            state={createTask}
          />
        )}
      />
      <Route
        path={`${match.path}/updatetask/Questionnaire`}
        component={() => (
          <Questionnaire
            transferMsg={transferMsg}
            path={match.url}
            taskID={taskID}
            state={updateTask}
          />
        )}
      />
      <Route
        path={`${match.path}/createtask/commission`}
        component={() => (
          <Commission transferMsg={transferMsg} path={match.url} />
        )}
      />
    </div>
  );
}

TaskBoard.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(TaskBoard);
