import React from "react";
import PropTypes from "prop-types";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import { withStyles } from "@material-ui/core/styles";
import DeleteOutlinedIcon from "@material-ui/icons/DeleteOutlined";
import EditIcon from "@material-ui/icons/Edit";

import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";

const styles = theme => ({
  paper: {
    maxWidth: 1000,
    margin: "auto",
    overflow: "hidden",
    marginTop: "1rem"
  },
  searchBar: {
    borderBottom: "1px solid rgba(0, 0, 0, 0.12)"
  },
  searchInput: {
    fontSize: theme.typography.fontSize
  },
  block: {
    display: "block"
  },
  contentWrapper: {
    margin: "10px 20px"
  },
  iconWrapper: {
    marginRight: "2px"
  },
  button: {
    margin: "0px 8px"
  }
});

function stringToDate(time) {
  //   2019-06-12T12:00  Date
  // let time = _time.toString();
  let year = time.slice(0, 4);
  let month = time.slice(5, 7);
  let day = time.slice(8, 10);
  let min = time.slice(11, 13);
  let sec = time.slice(14, 16);
  let date = new Date(year, month - 1, day, min, sec);
  return date;
}

function Content(props) {
  // use state 
  const [open_delete, setOpen_delete] = React.useState(false);
  // const [open_refresh, setOpen_refresh] = React.useState(false);
  const [open_stop, setOpen_stop] = React.useState(false);
  // 
  const [show, setShow] = React.useState("not null");

  const {
    classes,
    taskName,
    taskID,
    taskType,
    money,
    publishTime,
    endTime,
    finishedNumber,
    number,
    transferMsg,
    removeTask,
    modifyTaskState,
    downloadTaskData
  } = props;

  const [taskState, setTaskState] = React.useState("");

  const [taskButtonState, setTaskButtonState] = React.useState("");

  // 
  function handleClickOpen_delete() {
    setOpen_delete(true);
  }

  // 
  function handleClose_delete() {
    setOpen_delete(false);
  }

  // 
  function deleteToTrash() {
    setOpen_delete(false);
    removeTask(taskID);
    handleClose_delete(); // 
    setShow(null); // 
  }

  // 
  function handleTask() {
    setOpen_stop(true);
  }

  // 
  function handleTask_modify() {
    let state = "";
    if (taskButtonState === "") {
      state = "publish";
    } else if (taskButtonState === "") {
      state = "stop";
    } else if (taskButtonState === "") {
      state = "restart";
    }
    //
    modifyTaskState(taskID, state);
    if (taskButtonState === "") {
      setTaskButtonState("");
    } else if (taskButtonState === "") {
      setTaskButtonState("");
    } else if (taskButtonState === "") {
      setTaskButtonState("");
    }
    setOpen_stop(false);
  }

  // 
  function handleTask_stop() {
    setOpen_stop(false);
  }

  // 
  function editTask() {
    // ID
    transferMsg(taskID);
  }

  React.useEffect(() => {
    let publishDate = stringToDate(publishTime);
    let endDate = stringToDate(endTime);

    let currentDate = new Date();
    if (publishTime.length === 0 && endTime.length === 0) {
      setTaskState("");
      setTaskButtonState("");
    } else if (publishDate > currentDate) {
      setTaskState("");
      setTaskButtonState("");
    } else if (
      (publishDate <= currentDate && endDate > currentDate) ||
      (publishDate <= currentDate && endTime.length === 0)
    ) {
      setTaskState("");
      setTaskButtonState("");
    } else if (endDate < currentDate) {
      setTaskState("");
      setTaskButtonState("");
    }
  });

  // 
  function handleDownloadData() {
    downloadTaskData(taskID);
  }

  return (
    <div>
      {show !== null ? (
        <Paper className={classes.paper}>
          <AppBar
            className={classes.searchBar}
            position="static"
            color="default"
            elevation={0}
          >
            <Toolbar>
              <Grid container alignItems="center">
                <Grid item xs={8}>
                  <Typography variant="h5" display="block" gutterBottom>
                    {taskName}
                  </Typography>
                  <Typography variant="caption" display="block" gutterBottom>
                    ID: {taskID} | {taskState}
                  </Typography>
                </Grid>
                <Grid item xs={4} sm={1}>
                  <Tooltip title="">
                    <IconButton>
                      <EditIcon color="inherit" onClick={editTask} />
                    </IconButton>
                  </Tooltip>
                </Grid>
                <Grid item xs={4} sm={1}>
                  <Tooltip title="">
                    <IconButton>
                      <DeleteOutlinedIcon
                        color="inherit"
                        onClick={handleClickOpen_delete}
                      />
                    </IconButton>
                  </Tooltip>
                </Grid>
              </Grid>
            </Toolbar>
          </AppBar>
          <div className={classes.contentWrapper}>
            <Grid container>
              <Grid item xs={8}>
                <Typography variant="subtitle1" gutterBottom>
                  {taskType}
                </Typography>
                <Typography variant="subtitle1" gutterBottom>
                  {money} /
                </Typography>
                <Typography variant="subtitle1" gutterBottom>
                  {finishedNumber}/{number}
                </Typography>
              </Grid>
              <Grid item xs={4}>
                <Typography variant="subtitle1" gutterBottom>
                  {publishTime}
                </Typography>
                <Typography variant="subtitle1" gutterBottom>
                  {endTime}
                </Typography>
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleDownloadData}
                >
                  
                </Button>
                <Button
                  variant="contained"
                  color="secondary"
                  className={classes.button}
                  onClick={handleTask}
                >
                  {taskButtonState}
                </Button>
              </Grid>
            </Grid>
          </div>
          <Dialog
            open={open_delete}
            onClose={handleClose_delete}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
          >
            <DialogTitle id="alert-dialog-title">
              {""}
            </DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={deleteToTrash} color="primary">
                
              </Button>
              <Button onClick={handleClose_delete} color="primary" autoFocus>
                
              </Button>
            </DialogActions>
          </Dialog>
          <Dialog
            open={open_stop}
            onClose={handleTask_stop}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
          >
            <DialogTitle id="alert-dialog-title">
              {` ${taskButtonState} `}
            </DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleTask_modify} color="primary">
                
              </Button>
              <Button onClick={handleTask_stop} color="primary" autoFocus>
                
              </Button>
            </DialogActions>
          </Dialog>
        </Paper>
      ) : null}
    </div>
  );
}

Content.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Content);
