import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import QuestionnaireContent from "components/QuestionnaireContent/QuestionnaireContent";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { handleResponse, parseParams } from "variables/serverFunc.jsx";

const apiUrl = "https://littlefish33.cn:8080";

const styles = theme => ({
  root: {
    margin: "2px auto",
    maxWidth: "1300px",
    padding: "0px"
  },
  grid: {
    margin: "20px 0px 0px 0px"
  },
  container: {
    margin: "10px 0px 20px"
  },
  button: {
    margin: "10px 0 0 85%"
  }
});

/*
 *  *********************
 */
function stringToDate(time) {
  //   2019-06-12T12:00  Date
  // let time = _time.toString();
  let year = time.slice(0, 4);
  let month = time.slice(5, 7);
  let day = time.slice(8, 10);
  let min = time.slice(11, 13);
  let sec = time.slice(14, 16);
  let date = new Date(year, month - 1, day, min, sec);
  return date;
}

function judgeTime(time) {
  //   2019-06-12T12:00
  // time 
  // 
  // 
  // let time = _time.toString();
  if (time.length === 0) {
    return true;
  } else if (time.length === 16) {
    let currentTime = new Date();
    let cmpTime = stringToDate(time);
    if (cmpTime > currentTime) {
      return true;
    } else {
      return false;
    }
  } else {
    return false;
  }
}

function compareTime(publishTime, endTime) {
  // publishTimeendTime
  // 
  // 0.  1
  // 1.   0
  // 2.   1
  // 3.  2
  // 4.  1
  //  2019-06-12T12:00
  if (publishTime.length === 0 && endTime.length === 0) {
    return 1;
  } else if (publishTime.length === 0 && endTime.length !== 0) {
    return 0;
  } else if (publishTime.length !== 0 && endTime.length === 0) {
    return 1;
  } else {
    let date1 = stringToDate(publishTime.toString());
    let date2 = stringToDate(endTime.toString());
    if (date1 < date2) {
      return 1;
    } else {
      return 2;
    }
  }
}

function Questionnaire(props) {
  if (!localStorage.getItem("user-token")) {
    props.history.push("/login");
  }
  const { classes, transferMsg, taskID } = props;

  // 
  const [values, setValues] = React.useState({
    title: "",
    description: "",
    money: 0,
    number: 0,
    publishTime: "",
    endTime: ""
  });

  const [Content, setContent] = React.useState({
    maxID: 0,
    chooseData: []
  }); // 

  // create or update
  const [QState, setQState] = React.useState("");

  const submitQuestionnaire = () => {
    if (values.title === "") {
      alert("");
      return null;
    }

    if (values.money < 0) {
      alert("0");
      return null;
    }

    if (Content.chooseData.length === 0) {
      alert("");
      return null;
    }

    if (values.number <= 0) {
      alert("");
      return null;
    }

    if (judgeTime(values.publishTime) === false) {
      alert("");
      return null;
    }

    if (judgeTime(values.endTime) === false) {
      alert("");
      return null;
    }

    let timeState = compareTime(values.publishTime, values.endTime);
    if (timeState === 0) {
      alert("!");
      return null;
    }
    if (timeState === 2) {
      alert(" ");
      return null;
    }

    //  alert
    let currentMoney = localStorage.getItem("user-remain");
    let payMoney = values.money * values.number;
    if (currentMoney < payMoney) {
      alert(
        " \n " +
          payMoney +
          " ,  " +
          currentMoney.toString() +
          " "
      );
      return null;
    }

    // token
    let data = new FormData();
    data.append("money", currentMoney - payMoney);
    const requestUpdateMoney = {
      method: "POST",
      headers: {
        Authorization: "Bearer " + localStorage.getItem("user-token")
      },
      body: data
    };

    let isError = false;

    fetch(apiUrl + "/user/updatemoney", requestUpdateMoney)
      .then(handleResponse)
      .then(response => {
        if (response.code === 200) {
          localStorage.setItem("user-remain", currentMoney - payMoney);
        }
      })
      .catch(() => {
        alert("");
        isError = true;
        return null;
      });

    if (isError) return null;

    if (taskID.length === 14) {
      let allData = {
        taskName: values.title,
        taskID: taskID,
        inTrash: 0,
        taskType: "",
        creator: localStorage.getItem("userID"),
        description: values.description,
        money: parseInt(values.money),
        number: parseInt(values.number),
        finishedNumber: 0,
        publishTime: values.publishTime,
        endTime: values.endTime,
        chooseData: Content.chooseData
      };

      let data = new FormData();
      data.append("data", JSON.stringify(allData));
      data.append("id", taskID);

      const requestOptions = {
        method: "POST",
        headers: {
          Authorization: "Bearer " + localStorage.getItem("user-token")
        },
        body: data
      };

      // 
      fetch(apiUrl + "/questionnaire/update", requestOptions)
        .then(handleResponse)
        .then(response => {
          // console.log(response);
          if (response.code === 200) {
            alert(response.msg);
          } else {
            alert(response.msg);
          }
        });
    } else {
      let allData = {
        taskName: values.title,
        taskID: "",
        inTrash: 0,
        taskType: "",
        creator: localStorage.getItem("userID"),
        description: values.description,
        money: parseInt(values.money),
        number: parseInt(values.number),
        finishedNumber: 0,
        publishTime: values.publishTime,
        endTime: values.endTime,
        chooseData: Content.chooseData
      };

      let data = new FormData();
      data.append("data", JSON.stringify(allData));
      const requestOptions = {
        method: "POST",
        headers: {
          Authorization: "Bearer " + localStorage.getItem("user-token")
        },
        body: data
      };
      fetch(apiUrl + "/questionnaire/create", requestOptions)
        .then(handleResponse)
        .then(response => {
          if (response.code === 200) {
            alert(
              " " +
                parseInt(values.money) * parseInt(values.number) +
                ""
            );
          } else {
            alert("");
          }
        });
    }
  };

  const handleChange = name => event => {
    setValues({ ...values, [name]: event.target.value });
  };

  useEffect(() => {
    transferMsg("2Q"); // 
    if (taskID.length === 14) {
      // taskID
      setQState("");
      // taskID
      const requestOptions = {
        method: "POST",
        headers: {
          //Authorization: "Bearer " + localStorage.getItem("user-token"),
          Accept: "application/json",
          "content-type": "application/x-www-form-urlencoded"
        },
        body: parseParams({ id: taskID })
      };
      // console.log(requestOptions);
      fetch(apiUrl + "/questionnaire/select", requestOptions)
        .then(handleResponse)
        .then(response => {
          // console.log(response);
          setValues({
            title: response.taskName,
            description: response.description,
            money: response.money + "",
            number: response.number + "",
            publishTime: response.publishTime,
            endTime: response.endTime
          });
          setContent({
            maxID: 100,
            chooseData: response.chooseData
          });
        });
    }
    return () => {
      transferMsg("Return"); // 
    };
  }, []);

  return (
    <div className={classes.root}>
      <Grid container spacing={8}>
        <Grid item xs={12} sm={4}>
          <Typography variant="h5" gutterBottom>
            {QState}
          </Typography>
          <TextField
            label=""
            type="search"
            value={values.title}
            onChange={handleChange("title")}
            fullWidth="true"
            placeholder=""
            className={classes.textField}
            margin="normal"
            InputLabelProps={{
              shrink: true
            }}
          />
          <TextField
            label=""
            type="search"
            fullWidth="true"
            placeholder=""
            onChange={handleChange("description")}
            value={values.description}
            className={classes.textField}
            margin="normal"
            InputLabelProps={{
              shrink: true
            }}
          />
          <TextField
            label="/"
            type="number"
            className={classes.textField}
            placeholder=""
            fullWidth="true"
            onChange={handleChange("money")}
            value={values.money}
            InputLabelProps={{
              shrink: true
            }}
            margin="normal"
          />
          <TextField
            label=""
            type="number"
            className={classes.textField}
            placeholder=""
            onChange={handleChange("number")}
            value={values.number}
            fullWidth="true"
            InputLabelProps={{
              shrink: true
            }}
            margin="normal"
          />
          <form className={classes.container} noValidate>
            <TextField
              id="datetime-local"
              label=""
              type="datetime-local"
              fullWidth="true"
              onChange={handleChange("publishTime")}
              value={values.publishTime}
              className={classes.textField}
              InputLabelProps={{
                shrink: true
              }}
            />
          </form>
          <form className={classes.container} noValidate>
            <TextField
              id="datetime-local"
              label=""
              type="datetime-local"
              placeholder=""
              onChange={handleChange("endTime")}
              value={values.endTime}
              fullWidth="true"
              className={classes.textField}
              InputLabelProps={{
                shrink: true
              }}
            />
          </form>
          <Button
            variant="contained"
            color="primary"
            className={classes.button}
            onClick={submitQuestionnaire}
          >
            {QState}
          </Button>
        </Grid>
        <Grid item xs={12} sm={8}>
          <QuestionnaireContent
            title={values.title}
            description={values.description}
            Content={Content}
            setContent={setContent}
          />
        </Grid>
      </Grid>
    </div>
  );
}

Questionnaire.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Questionnaire);
