import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
// UI 
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import TextField from "@material-ui/core/TextField";
import Switch from "@material-ui/core/Switch";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import EditIcon from "@material-ui/icons/Edit";
import Tooltip from "@material-ui/core/Tooltip";

const style = theme => ({
  paper: {
    margin: "auto",
    maxWidth: "760px",
    padding: "20px"
  },
  essayQuestion: {
    margin: "15px"
  },
  button: {
    margin: "10px 0px 0px 5px"
  },
  list: {
    maxHeight: "400px",
    position: "relative",
    overflow: "auto"
  }
});

function QuestionaireContent(props) {
  const { classes, title, description, Content, setContent } = props;
  /***************************************************
   *   
   *****************************************************/
  // 
  /*
  const [Content, setContent] = React.useState({
    maxID: 0,
    chooseData: []
  }); // 
  */

  // map state
  const [load, setLoad] = React.useState(0);

  //   1   2   0 not show
  const [show, setShow] = React.useState(0);

  // 
  const [questionType, setQuestionType] = React.useState({
    essayQuestion: 1,
    singleChoice: 2,
    multiChoice: 3
  });

  // 
  const [required, setRequired] = React.useState({
    essayQuestion: false, // 
    choice: false, // 
    singleOrMulti: false // 
  });

  // 
  const [questionTitle, setQuestionTitle] = React.useState("");

  // 
  const [questionChoice, setQuestionChoice] = React.useState({
    maxID: 1, //  + 1 ID
    chooseData: [
      {
        id: 1,
        content: "" // 
      }
    ]
  });

  // updatetrue////update=false
  const [update, setUpdate] = React.useState(false);
  // Content
  const [updateIndex, setUpdateIndex] = React.useState(-1);

  /***************************************************
   *   
   *****************************************************/

  // ,
  const handleChange = name => event => {
    setRequired({ ...required, [name]: event.target.checked });
    setLoad(0);
  };

  // title state
  const handleTitlechange = name => event => {
    setQuestionTitle(event.target.value);
    setLoad(0);
  };

  // state
  const handelChoiceItemChange = id => event => {
    let tmpChoiceItem = questionChoice.chooseData;
    for (let i = 0; i < tmpChoiceItem.length; i++) {
      if (id === tmpChoiceItem[i].id) {
        tmpChoiceItem[i].content = event.target.value;
        break;
      }
    }
    setQuestionChoice({
      maxID: questionChoice.maxID,
      chooseData: tmpChoiceItem
    });
    setLoad(0);
  };

  // 
  const showEssayQuestion = () => {
    setQuestionTitle(""); // 
    setUpdate(false);
    setRequired({
      essayQuestion: false, // 
      choice: false, // 
      singleOrMulti: false // 
    });
    setShow(1);
  };

  // 
  const notShow = () => {
    setUpdate(false);
    setShow(0);
  };

  // , 
  const showChoice = () => {
    setQuestionChoice({
      maxID: 1,
      chooseData: [
        {
          id: 1,
          content: "" // 
        }
      ]
    });
    setRequired({
      essayQuestion: false, // 
      choice: false, // 
      singleOrMulti: false // 
    });
    setUpdate(false);
    setQuestionTitle(""); // 
    setShow(2);
  };

  //  
  const addEssayQuestion = () => {
    if (questionTitle === "") {
      alert("");
      return null;
    }
    if (update) {
      // 
      let tmpChooseData = Content.chooseData;
      tmpChooseData[updateIndex] = {
        titleNum: tmpChooseData[updateIndex].titleNum,
        title: questionTitle,
        id: tmpChooseData[updateIndex].id,
        dataType: questionType.essayQuestion,
        required: required.essayQuestion,
        dataContent: [{ id: 0, content: "" }]
      };
      setContent({ maxID: Content.maxID, chooseData: tmpChooseData });
    } else {
      let titleNum = Content.chooseData.length + 1;
      let id = Content.maxID + 1;
      let tmpChooseData = Content.chooseData;
      // 
      tmpChooseData.push({
        titleNum: titleNum,
        id: id,
        title: questionTitle, // state 
        dataType: questionType.essayQuestion,
        required: required.essayQuestion,
        dataContent: [{ id: 0, content: "" }]
      });
      setContent({ maxID: id, chooseData: tmpChooseData });
    }
    setQuestionTitle(""); // 
    setRequired({
      essayQuestion: false, // 
      choice: false, // 
      singleOrMulti: false // 
    });
    setLoad(0);
    setUpdate(false);
  };

  // 
  const addChoiceItem = () => {
    let tmpQuestionChoice = questionChoice.chooseData;
    let id = questionChoice.maxID + 1;
    tmpQuestionChoice.push({
      id: id,
      content: ""
    });
    setQuestionChoice({ maxID: id, chooseData: tmpQuestionChoice });
    setLoad(0);
  };

  // 
  const deleteChoiceItem = id => event => {
    if (questionChoice.chooseData.length === 1) {
      alert("");
      return null;
    }
    let tmpQuestionChoice = questionChoice.chooseData;

    let index = 0;
    for (let i = 0; i < tmpQuestionChoice.length; i++) {
      if (tmpQuestionChoice[i].id === id) {
        index = i;
        break;
      }
    }
    tmpQuestionChoice.splice(index, 1);
    setQuestionChoice({
      maxID: questionChoice.maxID,
      chooseData: tmpQuestionChoice
    });
    setLoad(0);
  };

  // 
  const addChoice = () => {
    if (questionTitle === "") {
      alert("");
      return null;
    }
    if (questionChoice.chooseData.length <= 1) {
      alert("");
      return null;
    }
    for (let i = 0; i < questionChoice.chooseData.length; i++) {
      if (questionChoice.chooseData[i].content === "") {
        alert("");
        return null;
      }
    }
    let tmpChooseData = Content.chooseData;
    if (update) {
      let dataType =
        required.singleOrMulti === true
          ? questionType.multiChoice
          : questionType.singleChoice;
      tmpChooseData[updateIndex] = {
        titleNum: tmpChooseData[updateIndex].titleNum,
        id: tmpChooseData[updateIndex].id,
        title: questionTitle,
        dataType: dataType,
        required: required.choice,
        dataContent: questionChoice.chooseData
      };
      setContent({ maxID: Content.maxID, chooseData: tmpChooseData });
    } else {
      // 
      let titleNum = Content.chooseData.length + 1;
      let id = Content.maxID + 1;
      let dataType =
        required.singleOrMulti === true
          ? questionType.multiChoice
          : questionType.singleChoice;
      tmpChooseData.push({
        titleNum: titleNum,
        id: id,
        title: questionTitle,
        dataType: dataType,
        required: required.choice,
        dataContent: questionChoice.chooseData
      });
      setContent({ maxID: id, chooseData: tmpChooseData });
    }
    // 
    setLoad(0);
    setQuestionTitle(""); // 
    setRequired({
      essayQuestion: false, // 
      choice: false, // 
      singleOrMulti: false // 
    });
    setQuestionChoice({
      maxID: 1,
      chooseData: [
        {
          id: 1,
          content: "" // 
        }
      ]
    });
    setUpdate(false);
  };

  // 
  const deleteQuestion = id => event => {
    //todo
    let tmpChooseData = Content.chooseData;
    let index = 0;
    for (let i = 0; i < tmpChooseData.length; i++) {
      if (id === tmpChooseData.id) {
        index = i;
        break;
      }
    }
    tmpChooseData.splice(index, 1); // 
    // titleNum-1
    for (let i = 0; i < tmpChooseData.length; i++) {
      if (i >= index) {
        tmpChooseData[i].titleNum = tmpChooseData[i].titleNum - 1;
      }
    }
    setContent({ maxID: Content.maxID, chooseData: Content.chooseData });
  };

  // 
  const editQuestion = id => event => {
    // 
    let tmpChooseData = Content.chooseData;
    // 
    let tmpQuestionTitle = questionTitle;
    // 
    let tmpQuestionChoice = questionChoice.chooseData;
    // 
    let index = 0;
    for (var i = 0; i < tmpChooseData.length; i++) {
      if (tmpChooseData[i].id === id) {
        index = i;
        break;
      }
    }
    // content
    setUpdateIndex(index);
    if (tmpChooseData[index].dataType === 1) {
      // 
      showEssayQuestion();
    } else {
      // 
      showChoice();
      // ,
      tmpQuestionChoice = tmpChooseData[index].dataContent;
      setQuestionChoice({
        maxID: questionChoice.maxID,
        chooseData: tmpQuestionChoice
      });
    }

    // 
    tmpQuestionTitle = tmpChooseData[index].title;
    setQuestionTitle(tmpQuestionTitle);
    // 
    if (tmpChooseData[index].required === 1) {
      if (tmpChooseData[index].dataType === 1) {
        setRequired({
          essayQuestion: true,
          choice: false,
          singleOrMulti: false
        });
      } else if (tmpChooseData[index].dataType === 2) {
        setRequired({
          essayQuestion: false,
          choice: true,
          singleOrMulti: false
        });
      } else if (tmpChooseData[index].dataType === 3) {
        setRequired({
          essayQuestion: false,
          choice: true,
          singleOrMulti: true
        });
      }
    }
    setUpdate(true);
  };

  return (
    <Paper className={classes.paper}>
      {!title && (
        <Typography variant="h5" gutterBottom align="center">
          ,~
        </Typography>
      )}
      <Typography variant="h5" gutterBottom align="center">
        {title}
      </Typography>
      <Typography gutterBottom>{description}</Typography>
      <List subheader={<li />} className={classes.list}>
        {Content.chooseData.map(item => {
          let required = "";
          if (item.required === true) {
            required = "";
          }
          let multiChoice = "";
          if (item.dataType === 3) {
            multiChoice = "";
          }

          return (
            <ListItem button key={item.id}>
              <li>
                <Typography variant="h6">
                  {item.titleNum}
                  {"."}
                  {item.title}
                  {required}
                  {multiChoice}
                </Typography>
                {item.dataType === 1 && (
                  <TextField
                    id="standard-bare"
                    className={classes.textField}
                    fullWidth="true"
                    margin="normal"
                    inputProps={{ "aria-label": "bare" }}
                  />
                )}
                {(item.dataType === 2 || item.dataType === 3) && (
                  <FormControl>
                    <RadioGroup>
                      {item.dataContent.map(_item => (
                        <div key={_item.id}>
                          <FormControlLabel
                            control={<Radio />}
                            label={_item.content}
                          />
                        </div>
                      ))}
                    </RadioGroup>
                  </FormControl>
                )}
              </li>
              <ListItemSecondaryAction>
                <Tooltip title="">
                  <IconButton onClick={editQuestion(item.id)}>
                    <EditIcon color="inherit" />
                  </IconButton>
                </Tooltip>
                <Tooltip title="">
                  <IconButton onClick={deleteQuestion(item.id)}>
                    <DeleteIcon />
                  </IconButton>
                </Tooltip>
              </ListItemSecondaryAction>
            </ListItem>
          );
        })}
      </List>
      <Grid container align="center">
        <Grid item xs={12} sm={6}>
          <Button
            variant="contained"
            color="secondary"
            onClick={showEssayQuestion}
          >
            
          </Button>
        </Grid>
        <Grid item xs={12} sm={6}>
          <Button variant="contained" color="secondary" onClick={showChoice}>
            
          </Button>
        </Grid>
      </Grid>
      {show === 1 && (
        <div className={classes.essayQuestion}>
          <Typography gutterBottom>
            
            <TextField
              id="standard-bare"
              className={classes.textField}
              placeholder=""
              onChange={handleTitlechange("title")}
              value={questionTitle}
              fullWidth="true"
              margin="normal"
              inputProps={{ "aria-label": "bare" }}
            />
          </Typography>
          <Typography gutterBottom>
            
            <TextField
              id="standard-bare"
              className={classes.textField}
              placeholder=""
              disabled
              margin="normal"
              fullWidth="true"
              inputProps={{ "aria-label": "bare" }}
            />
          </Typography>
          <Typography>
            
            <Switch
              checked={required.essayQuestion}
              onChange={handleChange("essayQuestion")}
              value="essayQuestion"
              inputProps={{ "aria-label": "secondary checkbox" }}
            />
          </Typography>
          <Button
            variant="contained"
            color="primary"
            className={classes.button}
            onClick={addEssayQuestion}
          >
            
          </Button>
          <Button
            variant="contained"
            color="secondary"
            className={classes.button}
            onClick={notShow}
          >
            
          </Button>
        </div>
      )}
      {show === 2 && (
        <div className={classes.essayQuestion}>
          <Typography gutterBottom>
            
            <TextField
              id="standard-bare"
              className={classes.textField}
              placeholder=""
              value={questionTitle}
              fullWidth="true"
              onChange={handleTitlechange("title")}
              margin="normal"
              inputProps={{ "aria-label": "bare" }}
            />
          </Typography>
          <Typography gutterBottom>
            
            {questionChoice.chooseData.map(item => (
              <Grid container key={item.id}>
                <Grid item xs={1}>
                  <Radio gutterBottom />
                </Grid>
                <Grid item xs={3}>
                  <TextField
                    id="standard-bare"
                    placeholder=""
                    margin="normal"
                    fullWiidth="true"
                    value={item.content}
                    onChange={handelChoiceItemChange(item.id)}
                    inputProps={{ "aria-label": "bare" }}
                    gutterBottom
                  />
                </Grid>
                <Grid item xs={2}>
                  <IconButton
                    aria-label="Delete"
                    onClick={deleteChoiceItem(item.id)}
                    value={item.id}
                  >
                    <DeleteIcon />
                  </IconButton>
                </Grid>
              </Grid>
            ))}
          </Typography>
          <Typography>
            {" "}
            <Fab
              size="small"
              color="secondary"
              aria-label="Add"
              className={classes.margin}
              onClick={addChoiceItem}
            >
              <AddIcon />
            </Fab>
          </Typography>
          <Typography>
            
            <Switch
              checked={required.choice}
              onChange={handleChange("choice")}
              value="choice"
              inputProps={{ "aria-label": "secondary checkbox" }}
            />
          </Typography>
          <Typography>
            
            <Switch
              checked={required.singleOrMulti}
              onChange={handleChange("singleOrMulti")}
              value="singleOrMulti"
              inputProps={{ "aria-label": "secondary checkbox" }}
            />
          </Typography>
          <Button
            variant="contained"
            color="primary"
            className={classes.button}
            onClick={addChoice}
          >
            
          </Button>
          <Button
            variant="contained"
            color="secondary"
            className={classes.button}
            onClick={notShow}
          >
            
          </Button>
        </div>
      )}
    </Paper>
  );
}

QuestionaireContent.prototype = {
  classes: PropTypes.object.isRequired
};

export default withStyles(style)(QuestionaireContent);
